body {
  background-image: url('P1050975.JPG');
  background-size: 100vmax;
  margin: 10px;
}

.App input,
.App button {
  margin: 20px;
  padding: 10px 20px;
  border: 2px solid gray;
  background-color: #fff;
  transition: .3s;
}

.App button {
  cursor: pointer;
}

.App button:hover {
  background-color: #000;
  color: white;
  border-color: black;
}